<template>
	<view>
		<!-- 折线图 -->
		<view class="line_chart_style">
			<view class="main_title_style">
				<text>数据统计</text>
			</view>
			<view class="charts_style">
				<qiun-data-charts background="rgba(49, 44, 52, 1.0)" :ontouch="true" type="line" :opts="opts"
					:chartData="chartData" />
			</view>
			<view class="boottom_style"></view>
		</view>
		<view class="uni-container">

			<view class="history_column">
				<view class="history_title">
					<text>历史记录</text>
				</view>
				<view class="history_filter">
					<view @click="radioClick(index)"
						:class="item.checked==true?'history_filter_item_active':'history_filter_item'"
						v-for="(item, index) in radios">
						<text>{{item.name}}</text>
					</view>
				</view>
			</view>
			<view>
				<uni-table class="history_table_style" :stripe="true">
					<uni-tr>
						<uni-th width="70" align="center">日期</uni-th>
						<uni-th width="55" v-for="item in equipment_factorName_group" align="center">{{item}}</uni-th>
					</uni-tr>
					<uni-tr align="center" v-for="(item,index) in equipment_data_group">
						<uni-td align="center" v-for="data in item">{{data}}</uni-td>
					</uni-tr>
				</uni-table>
			</view>



		</view>

	</view>
</template>

<script>
	export default {
		onLoad(parameter) {
			console.log("测试数据" + parameter.mn + parameter.accesstoken);
			this.equipment_model = parameter.mn;
			this.access_token = parameter.accesstoken;
			this.equipment_type = parameter.equipmentType;
		},
		data() {
			return {

				// 设备型号
				equipment_model: '',
				//accesstoken
				access_token: '',
				equipment_type: '',
				radios: [{
						checked: true,
						name: "实时",
					},
					{
						checked: false,
						name: "分钟"
					},
					{
						checked: false,
						name: "小时"
					}, {
						checked: false,
						name: "日"
					}
				],
				//设备因子名称组
				equipment_factorName_group: [],
				//设备数据组
				equipment_data_group: [],
				//图表时间节点
				charts_XCoords: [],
				//图表数据展示
				charts_data_group: [],
				testdata: [{
						"one": "2020-10-20",
						"two": "10",
						"three": "15.66",
						"four": "50",
						"five": "7",
						"six": "55",
						"seven": "50",
						"eight": "79",
						"nine": "60",
					}, {
						"one": "2020-10-20",
						"two": "10",
						"three": "15.66",
						"four": "50",
						"five": "7",
						"six": "55",
						"seven": "50",
						"eight": "79",
						"nine": "60",
					}, {
						"one": "2020-10-20",
						"two": "10",
						"three": "15.66",
						"four": "50",
						"five": "7",
						"six": "55",
						"seven": "50",
						"eight": "79",
						"nine": "60",
					}, {
						"one": "2020-10-20",
						"two": "10",
						"three": "15.66",
						"four": "50",
						"five": "7",
						"six": "55",
						"seven": "50",
						"eight": "79",
						"nine": "60",
					}, {
						"one": "2020-10-20",
						"two": "10",
						"three": "15.66",
						"four": "50",
						"five": "7",
						"six": "55",
						"seven": "50",
						"eight": "79",
						"nine": "60",
					},

				],
				chartData: {},
				//您可以通过修改 config-ucharts.js 文件中下标为 ['line'] 的节点来配置全局默认参数，如都是默认参数，此处可以不传 opts 。实际应用过程中 opts 只需传入与全局默认参数中不一致的【某一个属性】即可实现同类型的图表显示不同的样式，达到页面简洁的需求。
				opts: {
					color: ["#1890FF", "#91CB74", "#FAC858", "#EE6666", "#73C0DE", "#3CA272", "#FC8452", "#9A60B4",
						"#ea7ccc"
					],
					padding: [15, 10, 0, 15],
					legend: {
						// backgroundColor: rgba(120, 120, 120, 0.0),

					},
					background: 'rgba(78, 78, 78, 0.0)',
					enableScroll: true, //开启图表拖拽功能
					scrollAlign: 'left', //图表滚动条起始位置
					xAxis: {
						disableGrid: true,
						itemCount: 5, //x轴单屏显示数据的数量，默认为5个
						scrollShow: true, //新增是否显示滚动条，默认false
					},
					yAxis: {
						gridType: "dash",
						dashLength: 2
					},
					extra: {
						line: {
							type: "curve",
							width: 2
						}
					}
				}

			}
		},
		onReady() {
			this.syncApiData();

		},
		methods: {
			getServerData() {
				//模拟从服务器获取数据时的延时
				setTimeout(() => {
					//模拟服务器返回数据，如果数据格式和标准格式不同，需自行按下面的格式拼接
					let res = {
						categories: this.charts_XCoords,
						series: this.charts_data_group,
						// categories: ["0:00", "1:00", "2:00", "3:00", "4:00", "5:00", "6:00", "7:00", "8:00",
						// 	"9:00"
						// ],
						// series: [{
						// 		"name": "总烃",
						// 		"data": [35, 8, 25, 37, 4, 20, 35, 8, 25, 37]
						// 	},
						// 	{
						// 		"name": "非甲烷总烃",
						// 		"data": [70, 40, 65, 100, 44, 68, 70, 40, 65, 100]
						// 	},
						// 	{
						// 		"name": "甲烷",
						// 		"data": [100, 80, 95, 150, 112, 132, 100, 80, 95, 150]
						// 	}
						// ]
					};
					this.chartData = JSON.parse(JSON.stringify(res));
				}, 500);
			},
			radioClick(i) {
				let size = 0;
				let type = 3;
				this.radios.map((item, index) => {
					item.checked = index === i ? true : false
					// console.log(JSON.stringify(item)+index);
				})
				switch (i) {
					case 0:
						size = 60;
						type = 1;
						break;
					case 1:
						size = 10;
						type = 2;
						break;
					case 2:
						size = 24;
						type = 3;
						break;
					case 3:
						size = 30;
						type = 4;
						break;

				}
				this.syncGetHistoryPage(size, type);

			},


			//初始化数据
			async syncApiData() {
				await this.syncGetHistoryPage(24, 1);
			},

			//接收用户设备数据(size:返回几条数据，type：时间类型，日数据3，月记录4)
			syncGetHistoryPage(size, type, ) {
				return new Promise((resolve, reject) => {
					uni.request({
						// url: 'https://voc.16861.com/appapi?m=app&a=rtd&mn=SHAKS1234567898765432101&size=2&type=2&accesstoken=5aVy5cTv1655718104YIwWsldc859f1384212d35895a4ead128e75fa',
						url: 'https://voc.16861.com/appapi',
						data: {
							'm': 'app',
							'a': 'rtd',
							'mn': 'SHAKS1234567898765432101',
							'size': size,
							'type': type,
							'accesstoken': '5aVy5cTv1655718104YIwWsldc859f1384212d35895a4ead128e75fa',
						},
						method: "GET",
						header: {
							'Content-Type': 'application/json',
						},
						success: (res) => {
							if (res.statusCode != 200) {
								console.log("--------");
								console.log("网络请求失败");
								reject('error');
							}
							if (res.data.cp == null) {
								console.log("空数据");
								reject('error');
							}
							// console.log(JSON.stringify( res.data.cp));
							let list = res.data.cp;
							let equipment_factorName_group = [];
							//临时数组
							let temporaryArray = [];
							let equipment_data_group = [];
							let date = '';
							//获取图表数据
							let chart_data = [];
							let xCoords = [];
							//总烃

							//非甲烷总烃
							let nmhc = {};
							let nmhc_data = [];
							//甲烷

							//获取因子组数据
							for (let i in list[0]) {
								if (i == 'MN') {
									continue;
								} else if (i == 'DataTime') {
									// equipment_factorName_group.push('日期');
								} else {
									equipment_factorName_group.push(list[0][i].Name);
									switch (i) {
										case "a24088": {
											nmhc.name = '非甲烷总烃';
											break;
										}
									}
								}
							}
							this.equipment_factorName_group = equipment_factorName_group;
							//筛选数据
							for (let i in list) {
								for (let j in list[i]) {
									if (j == 'MN') {
										continue;
									} else if (j == 'DataTime') {
										switch (type) {
											case 3:
												date = list[i][j].slice(0, 4) + "-" + list[i][j].slice(
														4,
														6) + "-" + list[i][j].slice(6, 8) + "   " +
													list[
														i][j].slice(8, 10) + ":00";
												xCoords.push(list[i][j].slice(8, 10) + ":00");
												break;
											case 4:
												date = list[i][j].slice(0, 4) + "-" + list[i][j].slice(
													4,
													6) + "-" + list[i][j].slice(6, 8);
												xCoords.push(list[i][j].slice(4, 6) + "-" + list[i][j]
													.slice(6, 8));
												break;
											default:
												date = list[i][j].slice(0, 4) + "-" + list[i][j].slice(
														4,
														6) + "-" + list[i][j].slice(6, 8) + "   " +
													list[i][j].slice(8, 10) + ":" + list[i][j].slice(
														10, 12);
												xCoords.push(list[i][j].slice(8, 10) + ":" + list[i][j]
													.slice(10, 12));
												break;

										}
										temporaryArray.push(date);
									} else {
										if (list[i][j].Avg != null) {
											temporaryArray.push(list[i][j].Avg);
											switch (j) {
												case "a24088": {
													nmhc_data.push(list[i][j].Avg);
													break;
												}
											}
										}else{
											temporaryArray.push(list[i][j].Rtd);
											switch (j) {
												case "a24088": {
													nmhc_data.push(list[i][j].Rtd);
													break;
												}
											}
										}

									}
								}
								equipment_data_group.push(temporaryArray);
								temporaryArray = [];
							}

							if (nmhc != null || nmhc != '') {
								nmhc.data = nmhc_data;
								chart_data.push(nmhc);
							}
							this.equipment_data_group = equipment_data_group;
							this.charts_XCoords = xCoords;
							this.charts_data_group = chart_data;
							this.getServerData();
							// console.log(JSON.stringify(this.charts_data_group))
							resolve('success');
						},
						fail: (err) => {
							console.log(err);
							reject('error');
						},
					});
				})
			},

		}
	}
</script>

<style lang="scss">
	page {
		background-color: #000000;
		color: #ffffff;
	}

	.history_table_style {
		display: flex;

		.table {
			min-width: auto !important;
		}

	}

	.main_title_style {
		margin: 20px;
		font-size: 20px;
		font-weight: bold;
	}

	.uni-container {
		background-color: #312c34;
		width: 98%;
		margin: 10px 0 50px 1%;
		position: absolute;
		top: 375px;
		padding-bottom: 30px;
		border-radius: 25px;

		.uni-table {
			color: #ffffff;

			.uni-table-tr:nth-of-type(odd) {
				background-color: #0096db;
				color: #ffffff;
			}

			.uni-table-tr:nth-of-type(even) {
				background-color: #00668e;
				color: #ffffff;
			}

			.uni-table-th {
				color: #ffffff;

			}

			.uni-table-td {
				color: #ffffff;
			}
		}
	}



	.line_chart_style {
		background-color: #312c34;
		border-radius: 25px;
		width: 98%;
		position: absolute;
		margin-left: 1%;
		margin-top: 3%;

		.boottom_style {
			height: 25px;
		}

		.charts_style {
			width: 98%;
		}
	}

	.history_column {
		display: flex;
		width: 100%;
		height: auto;

		.history_title {
			width: 22%;
			height: auto;
			margin: 10px 20px;

			text {
				font-size: 20px;
				font-weight: bold;
			}
		}

		.history_filter {
			width: 78%;
			display: flex;
			flex: 4;
			align-items: center;
			justify-content: center;

			.history_filter_item {
				flex-grow: 1;
				border-radius: 25px;
				border-style: solid;
				border-width: 1rpx;
				border-color: #0096db;
				text-align: center;
				margin-right: 5px;
			}

			.history_filter_item_active {
				flex-grow: 1;
				border-radius: 25px;
				border-style: solid;
				border-width: 1rpx;
				border-color: #0096db;
				background-color: #0096db;
				text-align: center;
				margin-right: 5px;
			}
		}
	}
</style>
